.btn {
	display:flex;
	align-items:center;
	text-align:center;
	justify-content:center;
	--bs-btn-font-size:#{fontsizerem(16)};
	--bs-btn-font-weight:400;
	transition:.4s;
	width:auto;
	font-weight:500;
	@include fluidFontSize(toRem(16), toRem(24), toRem(991), toRem(1280), true);

	@include media-breakpoint-down(lg) {
		padding:toRem(10) toRem(16);
		border-radius:toRem(8);
	}

	span {
		&.icon-block {
			margin-right:10px;
		}

		&.name-block {
			+ .icon-block {
				margin-right:0;
				margin-left:10px;

				.icon {
					&-arrow-more {
						transition:.4s;
					}

					&-download-down {
						transition:.4s;
					}
				}
			}
		}
	}

	&:hover {
		.icon {
			&-arrow-more {
				transform:translate3d(0, 5px, 0);
			}

			&-download-down {
				transform:translate3d(0, 5px, 0);
			}
		}
	}

	&:focus,
	&:active {
		box-shadow:none;
	}

	&-warning {
		--bs-btn-bg:var(--bs-orange);
		--bs-btn-border-color:var(--bs-orange);
		--bs-btn-color:var(--white);

		--bs-btn-hover-color:var(--white);
		--bs-btn-hover-bg:var(--orange-dark);
		--bs-btn-hover-border-color:var(--orange-dark);

		--bs-btn-active-color:var(--white);
		--bs-btn-active-bg:var(--orange-dark);
		--bs-btn-active-border-color:var(--orange-dark);

		.icon {
			fill:var(--white);
		}

		&:hover,
		&:active,
		&:focus,
		&:not(:disabled):not(.disabled):active {
			box-shadow:0 0 10px 2px rgba(0, 0, 0, 0.05);
		}

		&.disabled,
		&:disabled,
		&.disabled:active,
		&:disabled:active {
			cursor:default;
			background-color:#E7E8EA;
			border-color:#E7E8EA;
			opacity:1;
			color:#B1B4BA;
		}
	}
}