.promo {
	&__block {
		background:linear-gradient(180deg, #2355D7 0%, #73B0FF 100%);
		@include fluidFontSize(toRem(16), toRem(24), toRem(991), toRem(1280), true);
		@include media-breakpoint-down(lg) {
			@include fontsizerem(16);
		}
		@include media-breakpoint-down(sm) {
			.container {
				width:344px;
				max-width:100%;
			}
		}
	}

	&-block {
		&__top {
			overflow:hidden;
			padding-top:toRem(80);
			padding-bottom:toRem(68);

			.row {
				align-items:flex-end;
			}

			&-content {
				@include make-col-ready;
				@include make-col(3.5, 8);
				@include media-breakpoint-down(lg) {
					@include make-col(12);
				}

				h1, .h1 {
					position:relative;
					z-index:1;
					margin:0 0 toRem(30);

					&::before {
						content:"";
						position:absolute;
						background:url("../pictures/bird-red.svg") 0 0 no-repeat;
						background-size:contain;
						pointer-events:none;
						width:38px;
						height:34px;
						top:-2px;
						left:8rem;
						z-index:-1;
						@include media-breakpoint-down(xl) {
							width:calcFluidFontSize(27px, 38px, 991px, 1200px, px);
							height:calcFluidFontSize(24px, 34px, 991px, 1200px, px);
							top:calcFluidFontSize(-7px, -2px, 991px, 1200px, px);
							left:calcFluidFontSize(70px, 128px, 991px, 1200px, px);
						}
						@include media-breakpoint-down(lg) {
							width:27px;
							height:24px;
							left:70px;
							top:-7px;
						}
					}
				}

				&-logo {
					margin-bottom:toRem(75);
					@include media-breakpoint-down(lg) {
						margin-bottom:0;
						position:absolute;
						left:toRem(40);
						top:toRem(30);
					}

					.icon {
						display:block;
						fill:var(--bs-white);
						@include media-breakpoint-down(lg) {
							max-height:32px;
							max-width:176px;
						}
					}
				}

				&-btn {
					display:flex;
					margin-bottom:toRem(21);
				}

				&-info {
					padding-top:toRem(94);
					@include media-breakpoint-down(lg) {
						padding-top:toRem(40);
					}

					a {
						color:var(--bs-white);
						text-decoration:none;
						display:inline-flex;
						align-items:center;

						span {
							&.icon-block {
								margin:0 toRem(10) 0 0;

								.icon {
									display:block;
									fill:var(--bs-white);
									@include media-breakpoint-down(lg) {
										width:30px;
										height:30px;
									}
								}
							}

							&.name-block {
								text-decoration:underline;
							}
						}
					}
				}
			}

			&-image {
				@include make-col-ready;
				@include make-col(4.5, 8);
				position:relative;
				pointer-events:none;
				@include media-breakpoint-down(lg) {
					@include make-col(12);
					order:-1;
					margin-bottom:toRem(45);
					display:flex;
					justify-content:center;
					padding-top:toRem(20);
				}

				&::after {
					content:"";
					position:absolute;
					background:url("../pictures/top-bg.webp") 50% 50% no-repeat;
					width:1177px;
					height:1025px;
					left:50%;
					top:50%;
					transform:translate3d(-50%, -50%, 0);
					pointer-events:none;
					mix-blend-mode:screen;
					@include media-breakpoint-down(lg) {
						background:url("../pictures/top-bg-lg.webp") 50% 50% no-repeat;
						width:554px;
						height:462px;
					}
				}


				picture {
					margin-right:-140px;
					position:relative;
					z-index:1;

					img {
						aspect-ratio:824 / 709;
					}

					@include media-breakpoint-down(xl) {
						margin-right:-100px;
					}
					@include media-breakpoint-down(lg) {
						margin:0;
						img {
							aspect-ratio:372 / 320;
						}
					}
				}
			}
		}

		&__steps {
			background:var(--bs-white);
			padding-top:toRem(99);
			color:var(--bs-blue);
			@include media-breakpoint-down(lg) {
				padding-top:0;
			}

			&-content {
				@include make-col-ready;
				@include make-col(4.2, 8);
				@include media-breakpoint-down(lg) {
					@include make-col(12);
				}

				h2, .h2 {
					position:relative;
					z-index:1;
					margin:0 0 toRem(55);

					&::before {
						content:"";
						position:absolute;
						background:url("../pictures/bird-red.svg") 0 0 no-repeat;
						background-size:contain;
						pointer-events:none;
						width:38px;
						height:34px;
						top:-2px;
						left:16.8rem;
						z-index:-1;
						@include media-breakpoint-down(xl) {
							width:calcFluidFontSize(27px, 38px, 991px, 1200px, px);
							height:calcFluidFontSize(24px, 34px, 991px, 1200px, px);
							top:calcFluidFontSize(-7px, -2px, 991px, 1200px, px);
							left:calcFluidFontSize(128px, 268px, 991px, 1200px, px);
						}
						@include media-breakpoint-down(lg) {
							width:27px;
							height:24px;
							left:70px;
							top:-7px;
						}
					}
				}
			}

			&-image {
				@include make-col-ready;
				@include make-col(3.8, 8);
				pointer-events:none;
				padding-top:toRem(95);
				@include media-breakpoint-down(lg) {
					@include make-col(12);
					order:-1;
					margin-bottom:toRem(22);
					display:flex;
					justify-content:center;
					padding-top:0;
				}


				picture {
					display:block;
					margin-right:-200px;

					img {
						aspect-ratio:900 / 953;
					}

					@include media-breakpoint-down(xl) {
						margin-right:-100px;
					}
					@include media-breakpoint-down(lg) {
						margin:0;
						img {
							aspect-ratio:404 / 423;
						}
					}
				}
			}

			&-bottom {
				@include make-col-ready;
				@include make-col(12);
				position:relative;
				margin-bottom:-92px;
				z-index:1;
				@include media-breakpoint-down(lg) {
					padding-top:140px;
					margin-bottom:-470px;
				}

				&-back {
					@include make-row(0);
					border-radius:toRem(20);
					overflow:hidden;
					background:#F4F6FA;
					color:#0A0A0B;
					@include media-breakpoint-down(lg) {
						overflow:visible;
					}
				}

				&-text {
					@include make-col-ready;
					@include make-col(4, 8);
					padding:toRem(38) toRem(43);
					@include media-breakpoint-down(lg) {
						@include make-col(12);
						text-align:center;
					}

					h3, .h3 {
						position:relative;
						z-index:1;
						margin:0 0 toRem(32);
					}

					.button {
						display:flex;
						@include media-breakpoint-down(lg) {
							justify-content:center;
						}

						.btn {
							width:auto;
						}
					}
				}

				&-image {
					@include make-col-ready;
					@include make-col(4, 8);
					position:relative;
					@include media-breakpoint-down(lg) {
						@include make-col(12);
						order:-1;
					}

					img {
						position:absolute;
						right:0;
						top:50%;
						transform:translate3d(0, -50%, 0);
						max-width:unset;
						@include media-breakpoint-down(xl) {
							max-width:700px;
						}
						@include media-breakpoint-down(lg) {
							display:block;
							position:relative;
							right:auto;
							top:auto;
							transform:none;
							max-width:100%;
							margin:-140px auto 0;
							aspect-ratio:240 / 479;
						}
					}
				}
			}
		}

		&__presents {
			padding-top:toRem(170);
			position:relative;
			overflow:hidden;
			@include media-breakpoint-down(lg) {
				padding-top:toRem(516);
			}

			&::before {
				content:"";
				position:absolute;
				left:50%;
				top:0;
				background:url("../pictures/presents-bg-top.webp") 50% 0 no-repeat;
				width:2400px;
				height:1393px;
				transform:translate3d(-50%, 0, 0);
				pointer-events:none;
				mix-blend-mode:screen;
				@include media-breakpoint-down(lg) {
					background:url("../pictures/presents-bg-top-lg.webp") 50% 0 no-repeat;
					width:1050px;
					height:838px;
				}
			}

			&::after {
				content:"";
				position:absolute;
				left:50%;
				bottom:0;
				background:url("../pictures/presents-bg-bottom.webp") 50% 0 no-repeat;
				width:2400px;
				height:1004px;
				transform:translate3d(-50%, 0, 0);
				pointer-events:none;
				mix-blend-mode:screen;
				@include media-breakpoint-down(lg) {
					background:url("../pictures/presents-bg-bottom-lg.webp") 50% 0 no-repeat;
					width:515px;
					height:175px;
				}
			}

			> .container {
				position:relative;
				z-index:1;
			}

			&-content {
				h2, .h2 {
					position:relative;
					z-index:1;
					margin-bottom:toRem(48);

					&::before {
						content:"";
						position:absolute;
						background:url("../pictures/bird-red.svg") 0 0 no-repeat;
						background-size:contain;
						pointer-events:none;
						width:38px;
						height:34px;
						top:-2px;
						left:11.2rem;
						z-index:-1;
						@include media-breakpoint-down(xl) {
							width:calcFluidFontSize(27px, 38px, 991px, 1200px, px);
							height:calcFluidFontSize(24px, 34px, 991px, 1200px, px);
							top:calcFluidFontSize(-7px, -2px, 991px, 1200px, px);
							left:calcFluidFontSize(85px, 179px, 991px, 1200px, px);
						}
						@include media-breakpoint-down(lg) {
							width:27px;
							height:24px;
							left:85px;
							top:-7px;
						}
					}
				}
			}
		}

		&__dates {
			background:var(--bs-white);
			padding-top:toRem(65);
			padding-bottom:toRem(95);
			color:#0A0A0B;

			&-content {
				margin:0 auto;
				max-width:784px;
				text-align:center;
				@include fluidFontSize(toRem(16), toRem(20), toRem(991), toRem(1200), true);

				p {
					a {
						color:var(--bs-orange);
					}
				}
			}
		}

		&__faq {
			background:linear-gradient(180deg, #2355D7 0%, #73B0FF 100%), #D9D9D9;
			padding-top:toRem(90);
			padding-bottom:toRem(260);
			@include media-breakpoint-down(lg) {
				padding-top:toRem(40);
				padding-bottom:50px;
			}

			a {
				color:var(--bs-white);
			}

			&-content {
				h2, .h2 {
					position:relative;
					z-index:1;
					margin-bottom:toRem(48);
					@include media-breakpoint-down(lg) {
						margin-bottom:toRem(10);
					}

					&::before {
						content:"";
						position:absolute;
						background:url("../pictures/bird-red.svg") 0 0 no-repeat;
						background-size:contain;
						pointer-events:none;
						width:38px;
						height:34px;
						top:-2px;
						left:4.4rem;
						z-index:-1;
						@include media-breakpoint-down(xl) {
							width:calcFluidFontSize(27px, 38px, 991px, 1200px, px);
							height:calcFluidFontSize(24px, 34px, 991px, 1200px, px);
							top:calcFluidFontSize(-7px, -2px, 991px, 1200px, px);
							left:calcFluidFontSize(30px, 70px, 991px, 1200px, px);
						}
						@include media-breakpoint-down(lg) {
							width:27px;
							height:24px;
							left:30px;
							top:-7px;
						}
					}
				}
			}
		}
	}

	&__steps {
		counter-reset:myCounter;
		margin-bottom:toRem(74);
		@include media-breakpoint-down(lg) {
			margin-bottom:toRem(28);
		}

		&-item {
			margin-bottom:toRem(32);
			padding-left:100px;
			padding-right:50px;
			position:relative;
			@include media-breakpoint-down(xl) {
				padding-right:20px;
			}
			@include media-breakpoint-down(lg) {
				padding-right:0;
				padding-left:49px;
				margin-bottom:toRem(20);
			}

			&:before {
				counter-increment:myCounter;
				content:counter(myCounter);
				position:absolute;
				left:0;
				top:0;
				color:var(--bs-orange);
				border:2px solid var(--bs-orange);
				border-radius:50%;
				width:74px;
				height:74px;
				@include fontsizerem(46);
				font-weight:900;
				display:flex;
				align-items:center;
				justify-content:center;
				line-height:1;
				@include media-breakpoint-down(lg) {
					width:24px;
					height:24px;
					@include fontsizerem(16);
					background:var(--bs-orange);
					border:none;
					color:var(--bs-white);
				}
			}

			&::after {
				content:"";
				position:absolute;
				width:2px;
				bottom:0;
				left:37px;
				top:36px;
				background:var(--bs-orange);
				@include media-breakpoint-down(lg) {
					left:12px;
				}
			}

			&:last-child {
				&::after {
					display:none;
				}
			}

			&-back {
				background:#F4F6FA;
				border-radius:toRem(20);
				padding:toRem(30) toRem(50) toRem(30) toRem(32);
				color:#0A0A0B;
			}

			&-title {
				@include fluidFontSize(toRem(16), toRem(24), toRem(991), toRem(1200), true);
				font-weight:900;
				line-height:108%;
				margin:0 0 toRem(28);
				position:relative;

				&::after {
					content:"";
					position:absolute;
					left:0;
					width:100px;
					height:5px;
					border-radius:toRem(3);
					background:#2B61EC;
					bottom:-20px;
				}
			}

			&-text {
				@include fluidFontSize(toRem(14), toRem(16), toRem(991), toRem(1200), true);
				line-height:128%;
			}
		}
	}

	&__presents {
		display:grid;
		gap:toRem(50);
		margin-bottom:toRem(104);
		@include media-breakpoint-down(xl) {
			gap:toRem(40);
		}
		@include media-breakpoint-down(lg) {
			gap:toRem(30);
		}
		@include media-breakpoint-down(md) {
			gap:toRem(20);
		}

		&-row {
			display:flex;
			gap:toRem(50);
			@include media-breakpoint-down(xl) {
				gap:toRem(40);
			}
			@include media-breakpoint-down(lg) {
				flex-wrap:wrap;
				gap:toRem(30);
			}
			@include media-breakpoint-down(md) {
				gap:toRem(20);
			}
		}

		&-item {
			min-height:180px;
			display:flex;
			align-items:center;
			justify-content:center;
			transition:all 0.3s ease;

			&.min {
				width:min(480px, 100%);
				@include media-breakpoint-down(lg) {
					width:calc(50% - #{toRem(15)});
				}
				@include media-breakpoint-down(md) {
					//width:calc(50% - #{toRem(10)});
					width:100%;
				}
				@include media-breakpoint-down(sm) {
					width:100%;
				}
			}

			&.mid {
				width:min(748px, 100%);
				@include media-breakpoint-down(lg) {
					width:calc(50% - #{toRem(15)});
				}
				@include media-breakpoint-down(md) {
					//width:calc(50% - #{toRem(10)});
					width:100%;
				}
				@include media-breakpoint-down(sm) {
					width:100%;
				}
			}

			&.wide {
				width:100%;
				@include media-breakpoint-down(md) {
					.promo__presents-item {
						&-back {
							background:#4774E8;
							border-radius:toRem(20);
							display:flex;
							flex-direction:column;
							overflow:visible;
						}

						&-title {
							position:relative;
							inset:unset;
							padding:toRem(16) toRem(38);
							order:-1;
						}

						&-image {
							height:310px;
							position:relative;

							&::before {
								content:"";
								position:absolute;
								left:50%;
								top:50%;
								transform:translate3d(-50%, -50%, 0);
								background:url("../pictures/present-5-lg-bg.webp") 50% 0 no-repeat;
								pointer-events:none;
								mix-blend-mode:screen;
								width:450px;
								height:406px;
								max-width:100%;
							}

							img {
								position:absolute;
								left:50%;
								top:50%;
								transform:translate3d(-50%, -50%, 0);
								width:auto;
								max-width:unset;
								z-index:1;
							}
						}
					}
				}
			}

			&-back {
				position:relative;
				border-radius:toRem(20);
				overflow:hidden;
				width:100%;

				&:hover {
					img {
						transform:scale(1.06);

						.wide & {
							transform:scale(1.02);
							@include media-breakpoint-down(md) {
								transform:translate3d(-50%, -50%, 0);
							}
						}
					}
				}
			}

			&-image {
				img {
					display:block;
					width:100%;
					transition:1s;
					position:relative;
					@include media-breakpoint-down(lg) {
						aspect-ratio:320 / 345;
						.wide & {
							aspect-ratio:1278 / 418;
						}
					}
					@include media-breakpoint-down(md) {
						.wide & {
							aspect-ratio:450 / 406;
						}
					}
				}
			}

			&-title {
				position:absolute;
				inset:0;
				padding:toRem(38);
				display:flex;
				justify-content:flex-start;
				align-items:flex-end;
				//@include fluidFontSize(toRem(24), toRem(32), toRem(991), toRem(1200), true);
				@include fontsizerem(32);
				font-weight:900;
				line-height:128%;
				@include media-breakpoint-down(md) {
					padding:toRem(24) toRem(30);
				}

				.wide & {
					align-items:center;
					text-transform:uppercase;
					@include fluidFontSize(toRem(42), toRem(120), toRem(991), toRem(1200), true);
					line-height:108%;
					padding-right:40%;
					@include media-breakpoint-down(lg) {
						justify-content:center;
						text-align:center;
					}
					@include media-breakpoint-down(md) {
						//@include fontsizerem(32);
						padding:toRem(24) toRem(30);
						text-transform:none;
					}
				}

				a {
					color:var(--bs-white);
					text-decoration:none;

					&:after {
						content:"";
						position:absolute;
						inset:0;
					}
				}
			}
		}
	}

	&__dates {
		margin-top:toRem(36);
		margin-bottom:toRem(36);

		+ .promo__dates {
			margin-top:0;
		}

		&-back {
			background:#F4F6FA;
			border-radius:toRem(20);
			position:relative;

			.block-ready & {
				.promo__dates {
					&-header {
						background:var(--bs-blue);
					}

					&-show {
						.open-list {
							background:var(--bs-orange);
						}
					}
				}
			}

			.main-prize & {
				.promo__dates {
					&-header {
						background:var(--bs-orange);
					}
				}
			}
		}

		&-show {
			position:absolute;
			left:50%;
			bottom:-12px;
			width:24px;
			height:24px;
			margin-left:-12px;

			.open-list {
				display:flex;
				width:24px;
				height:24px;
				align-items:center;
				justify-content:center;
				background:#CBCBCB;
				transition:.4s;
				border-radius:50%;

				.block-ready & {
					cursor:pointer;
				}

				.icon {
					fill:var(--bs-white);
					display:block;
					transition:transform 0.3s ease;

					.active & {
						transform:rotate(180deg);
					}
				}
			}
		}

		&-header {
			background:#747474;
			color:var(--bs-white);
			@include fluidFontSize(toRem(16), toRem(20), toRem(991), toRem(1200), true);
			transition:.4s;
			border-radius:toRem(20) toRem(20) 0 0;
			padding:toRem(9) toRem(20);
			font-weight:900;
		}

		&-content {
			padding:toRem(28);
			@include media-breakpoint-down(sm) {
				padding:toRem(12) toRem(12) toRem(24);
			}
		}

		&-title {
			@include fontsizerem(40);
			font-weight:500;
			text-align:center;
		}

		&-hidden {
			max-height:0;
			opacity:0;
			overflow:hidden;
			transform:translateY(-10px);
			transition:0.3s ease;

			.active & {
				max-height:3000px;
				opacity:1;
				transform:translateY(0);
			}
		}

		&-list {
			max-width:482px;
			margin:0 auto toRem(42);

			table {
				margin:0;

				th,
				td {
					&:first-child {
						text-align:left;
					}
				}
			}
		}

		&-button {
			display:flex;
			justify-content:center;
		}
	}

	&__info {
		display:flex;
		flex-direction:column;
		gap:toRem(20);

		&-item {
			@include media-breakpoint-down(md) {
				max-width:320px;
				margin:0 auto;
			}

			&-back {
				position:relative;
				color:var(--bs-white);

				.item-1 & {
					overflow:hidden;
					border-radius:toRem(20);
					@include media-breakpoint-down(md) {
						overflow:visible;
					}
				}
			}

			&-bg {
				overflow:hidden;
				background:#2557D8;
				border-radius:toRem(20);

				img {
					display:block;

					.item-1 & {
						aspect-ratio:760 / 512;
						@include media-breakpoint-down(md) {
							aspect-ratio:320 / 335;
						}
					}

					.item-2 & {
						aspect-ratio:760 / 182;
						@include media-breakpoint-down(md) {
							aspect-ratio:320 / 380;
						}
					}
				}
			}

			&-image {
				pointer-events:none;
				position:absolute;
				bottom:0;
				left:0;
				display:flex;
				justify-content:flex-end;
				align-items:flex-start;

				img {
					display:block;
				}

				.item-1 & {
					top:48px;
					right:0;
					justify-content:center;
					@include media-breakpoint-down(lg) {
						align-items:center;
					}
					@include media-breakpoint-down(md) {
						top:46px;
					}

					img {
						aspect-ratio:612 / 464;
						@include media-breakpoint-down(md) {
							aspect-ratio:320 / 289;
							max-width:unset;
						}
					}
				}

				.item-2 & {
					top:-28px;
					right:53px;
					@include media-breakpoint-down(md) {
						top:auto;
						right:34px;
						bottom:-60px;
					}

					img {
						aspect-ratio:211 / 223;
					}
				}
			}

			&-body {
				position:absolute;
				inset:0;
				text-align:left;

				.item-1 & {
					padding:toRem(30) toRem(26) toRem(20);
					display:flex;
					flex-direction:column;
				}

				.item-2 & {
					padding:toRem(30) toRem(45);
					display:flex;
					flex-direction:column;
					justify-content:center;
					@include media-breakpoint-down(md) {
						padding:toRem(30);
						justify-content:flex-start;
					}
				}
			}

			&-title {
				.item-1 & {
					@include fontsizerem(50);
					font-weight:900;
					line-height:108%;
					text-transform:uppercase;
				}

				.item-2 & {
					@include fontsizerem(32);
					font-weight:900;
					line-height:108%;
					margin-bottom:toRem(12);

					@include media-breakpoint-down(md) {
						@include fontsizerem(24)
					}

					span {
						color:var(--bs-orange);
					}
				}
			}

			&-desc {
				@include fontsizerem(16);
				font-weight:500;
				line-height:128%;
			}

			&-bottom {
				margin-top:auto;
				display:flex;
				align-items:center;
				gap:toRem(25);
				@include media-breakpoint-down(md) {
					flex-direction:column;
					align-items:flex-start;
				}
			}

			&-date {
				@include fontsizerem(32);
				line-height:108%;
			}

			&-text {
				@include fontsizerem(12);
				font-weight:500;
				line-height:128%;
				max-width:450px;
				@include media-breakpoint-down(md) {
					order:-1;
				}
			}
		}
	}

	&__faq {
		counter-reset:my-f-counter;

		&-item {
			border-bottom:1px solid var(--bs-white);
			padding-top:1.333333em;
			padding-bottom:.5em;

			&-back {
			}

			&-question {
				@include fluidFontSize(toRem(16), toRem(35), toRem(991), toRem(1200), true);
				font-weight:500;
				line-height:pxToEm(38, 25);
				margin-bottom:1em;
				@include media-breakpoint-down(lg) {
					margin-bottom:.5em;
				}

				a {
					color:var(--bs-white);
					display:flex;
					align-items:center;
					text-decoration:none;

					&::before {
						counter-increment:my-f-counter;
						content:counter(my-f-counter);
						display:inline-flex;
						align-items:center;
						justify-content:center;
						line-height:1;
						font-size:1em;
						margin-right:0.5em;
						border:1px solid currentColor;
						aspect-ratio:1/1;
						width:1.4856em;
						height:1.4856em;
						border-radius:50%;
						font-weight:500;
						font-feature-settings:'lnum' on;
						@include media-breakpoint-down(lg) {
							width:28px;
							height:28px;
						}
					}

					&::after {
						flex-shrink:0;
						width:1.2em;
						height:.689em;
						margin-left:auto;
						content:"";
						background-image:url("data:image/svg+xml;charset=UTF-8,%3csvg width='41' height='23' viewBox='0 0 41 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M21.6026 22.4425L40.1876 2.937C40.4978 2.61154 40.6709 2.17915 40.6709 1.7295C40.6709 1.27985 40.4978 0.847462 40.1876 0.522001L40.1666 0.500999C40.0162 0.342678 39.8351 0.21661 39.6345 0.130461C39.4338 0.0443134 39.2177 -0.000112534 38.9993 -0.000112534C38.7809 -0.000112534 38.5648 0.0443134 38.3642 0.130461C38.1635 0.21661 37.9825 0.342678 37.8321 0.500999L20.3321 18.869L2.83907 0.500999C2.68866 0.342678 2.50761 0.21661 2.30695 0.130461C2.10629 0.0443134 1.89019 -0.000112534 1.67181 -0.000112534C1.45344 -0.000112534 1.23735 0.0443134 1.03668 0.130461C0.836014 0.21661 0.654972 0.342678 0.504566 0.500999L0.483562 0.522001C0.173306 0.847462 0.000228882 1.27985 0.000228882 1.7295C0.000228882 2.17915 0.173306 2.61154 0.483562 2.937L19.0686 22.4425C19.232 22.614 19.4286 22.7506 19.6464 22.8439C19.8642 22.9372 20.0986 22.9854 20.3356 22.9854C20.5725 22.9854 20.807 22.9372 21.0248 22.8439C21.2426 22.7506 21.4391 22.614 21.6026 22.4425Z' fill='white' style='fill:white;fill-opacity:1;'/%3e%3c/svg%3e ");
						background-repeat:no-repeat;
						background-size:contain;
						transition:.4s;
						@include media-breakpoint-down(lg) {
							width:12px;
							height:7px;
						}

						.active & {
							transform:rotate(180deg);
						}
					}
				}
			}

			&-answer {
				max-height:0;
				overflow:hidden;
				opacity:0;
				transform:translateY(-10px);
				transition:0.1s ease;
				margin-bottom:0;
				position:relative;

				.active & {
					max-height:2000px;
					opacity:1;
					transform:translateY(0);
					margin-bottom:1em;
					transition:.4s ease;
				}

				a {
					transition:.3s;

					&:hover {
						color:var(--bs-orange);
					}
				}
			}
		}
	}
}