:root {
	--btn-primary:#{$blue};
	--btn-primary-border:var(--btn-primary);
	--btn-primary-hover:#{darken($blue,10%)};
	--btn-default-hover:#{darken(#00a5ee,10%)};
	--btn-alert-hover:#{darken($red,20%)};
	--blue-hover:#{darken(#00a5ee,10%)};
	--base-1-bg:rgba(0, 0, 0, .9);
	--base-1:#161615;
	--base-2:#{lighten(#161615,30%)};
	--bg-color:#{$body-bg};
	--grey-bg:rgba(0, 0, 0, .8);

	--blue-light:#C1DAEC;
	--white:#{$white};
	--green-dark:#{darken($green,10%)};

	--orange-dark:#{darken($orange,10%)};
	--grey:#EFEFEF;
	--light-grey:#f7f7f7;
	--text-color:#{$body-color};
	--swiper-theme-color:#4AABC4;
}

::selection {
	background:rgba(168, 189, 216, 0.37);
	color:var(--bs-body-color);
}


* {
	scrollbar-color:#5A5959 #D9D9D9;
	scrollbar-width:thin;
}

mark {
	padding:0 .2rem;
	background:rgba(168, 189, 216, 0.37);
}

html, body {
	height:100%;
	background:#2355D7;
}

body {
	-webkit-font-smoothing:antialiased;
	min-height:100vh;
}

@include media-breakpoint-down(sm) {
	.row {
		@include make-row(20px);
	}
	.col {
		@include make-col-ready();
	}
}

.hidden-svg {
	display:none;
}

.layout {
	flex-direction:column;
	display:flex;
	width:100%;
	position:relative;
	overflow:hidden;
	min-height:100vh;
	margin:0 auto;

	a {
		text-underline-offset:4px;
	}
}

.wrapper {
	flex-direction:column;
	display:flex;
	width:100%;
	min-height:100vh;
	margin-left:auto;
	margin-right:auto;
	position:relative;
	letter-spacing:-0.02em;
}


.main-block {
	margin:0 auto;
	width:100%;
	flex:1 1 auto;
}