.content {
	&-block {
		position:relative;
		width:100%;
		padding:0 calc(var(--bs-gutter-x) * 0.5);

		h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
			&.center {
				text-align:center;
			}

			&.red {
				color:var(--bs-red);
			}

			&.orange {
				color:var(--bs-orange);
			}

			&.blue {
				color:var(--bs-blue);
			}
		}

		.h1, h1 {
			@include fluidFontSize(toRem(40), toRem(70), toRem(991), toRem(1200), true);
			font-weight:900;
			line-height:108%;
			margin-bottom:toRem(20);
		}

		h2, .h2 {
			@include fluidFontSize(toRem(32), toRem(64), toRem(991), toRem(1200), true);
			font-weight:900;
			line-height:108%;
			margin-bottom:toRem(20);
		}

		h3, .h3 {
			@include fluidFontSize(toRem(24), toRem(32), toRem(991), toRem(1200), true);
			font-weight:900;
			line-height:108%;
			margin-bottom:toRem(16);
			@include media-breakpoint-down(sm) {
				margin-bottom:toRem(10);
			}
		}

		p {
			margin-bottom:toRem(12);

			+ ul {
				margin-top:-.5rem;
			}
		}

		img {
			@include img-fluid;
			user-select:none;
			pointer-events:none;
		}

		table {
			&.table {
				@include fluidFontSize(toRem(16), toRem(20), toRem(991), toRem(1200), true);

				thead {
					tr {
						th {
							font-weight:500;
							border:none;
							border-bottom:1px solid rgba(168, 168, 168, 1);

							p {
								margin:0;
							}

							&.td-left {
								text-align:left;
							}

							&.td-center {
								text-align:center;
							}

							&.td-middle,
							&.vcenter {
								vertical-align:middle;
							}

							&.fw400 {
								font-weight:400;
								white-space:nowrap;
							}

							&.wsnwr {
								white-space:nowrap;
							}
						}
					}
				}

				tbody {
					tr {
						td {
							font-weight:500;
							border:none;
							border-bottom:1px solid rgba(168, 168, 168, 1);

							&.td-center {
								text-align:center;
							}

							&.td-middle,
							&.vcenter {
								vertical-align:middle;
							}

							&.wsnwr {
								white-space:nowrap;
							}
						}
					}
				}
			}
		}

		img {
			user-select:none;
			pointer-events:none;

			&[align=left] {
				margin:0 $grid-gutter-width $grid-gutter-width 0;
			}

			&[align=right] {
				margin:0 0 $grid-gutter-width $grid-gutter-width;
			}
		}
	}
}