:root {
	@each $name, $value in $grid-breakpoints {
		--#{$prefix}breakpoint-#{$name}: #{$value};
	}
}

@if $enable-grid-classes {
	.row {
		@include make-row();

		> * {
			@include make-col-ready();
		}
	}
}

