.icon {
	display:inline-block;
}

@function toRem($value) {
	// Делим значение пикселя на 16, чтобы получить значение rem, и умножаем на 1rem, чтобы установить единицу [4](https://uiduck.com/posts/convert-px-to-rem-in-scss/)
	$remValue: math.div($value, 16) * 1rem;
	// Возвращаем рассчитанное значение rem
	@return $remValue;
}

@function pxToEm($px, $font-size: 16) {
	$remValue: math.div($px, $font-size) * 1em;
	@return $remValue;
}

@function strip-unit($number) {
	@if type-of($number) == 'number' and not unitless($number) {
		@return math.div($number, ($number * 0 + 1));
	}

	@return $number;
}

@function calcFluidFontSize($f-min, $f-max, $w-min, $w-max, $units: rem) {
	$f-min: strip-unit($f-min);
	$f-max: strip-unit($f-max);
	$w-min: strip-unit($w-min);
	$w-max: strip-unit($w-max);

	$k:  math.div(($f-max - $f-min),($w-max - $w-min));
	$b: $f-min - $k * $w-min;

	$b: $b + $units;

	@return calc( #{$k} * 100vw + #{$b} );
}

@mixin fluidFontSize($f-min, $f-max, $w-min, $w-max, $fallback: false) {
	font-size: $f-min;

	@media (min-width: $w-min) {
		@if ($fallback) {
			font-size: $fallback;
		}
		font-size: calcFluidFontSize($f-min, $f-max, $w-min, $w-max, rem);
	}
	@media (min-width: $w-max) {
		font-size: $f-max;
	}
}

@function fontsizerem($fs:16) {
	$f:math.div($fs, 16);
	@return $f + rem;
}

@mixin fontsizerem($fs:16) {
	$f:math.div($fs, 16);
	font-size:$f + rem;
}

@mixin fontsize($lg:22,$md:$lg - 2px,$sm:$lg - 4px,$xs:$lg - 6px) {
	@include fontsizerem($lg);
	@include media-breakpoint-down(lg) {
		@include fontsizerem($md);
	}
	@include media-breakpoint-down(md) {
		@include fontsizerem($sm);
	}
	@include media-breakpoint-down(sm) {
		@include fontsizerem($xs);
	}
}